@import "~materialize-css/dist/css/materialize.min.css";


.list-film{
  margin-top: 100px;
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  column-gap: 10px;
  row-gap: 1em;
}

.card-film_img{
  width: 178px;
  height: 248px;
}

.card-film{
  margin: 0 auto;
}

.card-film_title, .card-film_action {
  text-align: center;
  text-transform: uppercase;
  padding: 5px;
}

.title{
  color: black;
  
}

.card-film_title a {
  display: inline-block;
  position: relative;
  text-decoration: none;
}
.card-film_title a:before {
  display: block;
  position: absolute;
  content: "";
  height: 2px;
  width: 0;
  background: orange;
  transition: width 0.3s ease-in-out, left 0.3s ease-in-out;
  left: 50%;
  bottom: 0;
}
.card-film_title a:after {
  display: block;
  position: absolute;
  content: "";
  height: 2px;
  width: 0;
  background-color: orange;
  transition: width 0.3s ease-in-out;
  left: 50%;
  bottom: 0;
}

.card-film_title a:hover:before {
  width: 50%;
  left: 0;
}
.card-film_title a:hover:after {
  width: 50%;
}

.buy {
  color: orange;
}

.card-film_action:hover {
  background-color: orange;
  color: black;
  border-radius: 5px;
  transition: all .3s ease-in-out;
}

.card-film_action:hover .buy{
  color: black;
  transition: all .3s ease-in-out;
}

.film-info{
  margin-top: 40px;
  display: flex;
}

.film-content {
  margin-left: 30px;
}

.film-baner img{
  width: 400px;
}

.film-content dt {
  font-weight: 400;
  font-size: 12px;
  color: #b6b6b6;
}
.film-content dd {
  margin-left: 0;
  margin-bottom: 10px;
  font-size: 16px;
  line-height: 24px;
}

.film-text {
  margin-left: 30px;
}

.tabs .tab a {
  color: rgba(66, 66, 66, .8)
}

.tabs .tab a:hover, .tabs .tab a.active {
  background-color: transparent;
  color: #424242;
}


.tabs .tab a:focus, .tabs .tab a:focus.active {
  background-color: rgba(66, 66, 66, .2);
}

.tabs .indicator {
  background-color: orange;
}

.col.tabs-content{
  padding: 10px;
}

#film-reviews {
  margin-bottom: 50px;
}

#film-reviews form, .response-box {
  max-width: 700px;
  margin: 20px auto 0;
  padding: 15px;
  box-shadow: 0 2px 8px rgba(0,0,0,.3);
  border-radius: 5px;
}

#film-reviews .row{
  margin-bottom: 0px;
}

#film-reviews .input-field .prefix.active {
  color: orange;
}

#film-reviews input{
  border-bottom: 1px solid #424242;
  box-shadow: 0 1px 0 0 #424242;
}

#film-reviews textarea{
  border-bottom: 1px solid #424242;
}

#film-reviews label{
  color: #424242;
}

.user-name {
  font-size: 20px;
  font-weight: bold;
  color: #424242;
}

.user-respsonse {
  padding-left: 10px;
}

.video-container {
  margin-bottom: 60px;
}

.places__inner {
  margin-top: 20px;
}

.box-img {
  max-width: 585px;
  margin: 0 auto;
  text-align: center;
  position: relative;
}

.screen {
  position: absolute;
  top: -11px;
  left: calc(50% - 25.8px);
  background-color: #fff;
  padding: 5px;
}

.caption.left {
  justify-self: start;
}

.caption.right {
  justify-self: end;
}

.scheme-container {
  max-width: 585px;
  margin: 20px auto 0;
  display: grid;
  grid-template-columns: 85px repeat(9, 16px) 40px  repeat(10, 16px) 64px;
  grid-template-rows: repeat(14, 16px);
  grid-gap: .3em;

}

.place {
  background-color: rgb(250, 194, 90);
  color: rgb(250, 194, 90);
  width: 16px;
  height: 16px;
  border-radius: 2px;
  text-align: center;
  cursor: pointer;
  font-size: 10px;
}

.place:hover {
  color: #424242;
}

.busy {
  cursor: default;
  background-color: #424242;
  color: #424242;
}

.busy:hover {
  color: rgb(250, 194, 90);
}

.btn-success, .cartTitle {
  margin-top: 20px;
}